.launch-controller-grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 22px;
  max-width: 530px;

  .text-block {
    margin-bottom: 1rem;
    .paragraph {
      margin-top: 1rem;

      > a {
        color: $blue_color;
      }
    }
  }

  > span {
    :nth-of-type(2) {
      margin-left: 15px;
    }
  }
}
